
import CryptoModal from './CryptoModal.vue';
import { ref } from 'vue';

export default {
  props: {
    coins: Object,
  },
  data() {
    return {
      isModalOpen: false,
      selectedCoin: null,
    };
  },
  methods: {
    openModal(coin) {
      this.selectedCoin = coin;
      this.isModalOpen = true;
    },
    closeModal() {
      this.isModalOpen = false;
    },
  },
  setup() {
    const modalActive = ref(false);

    const toggleModal = () => {
      modalActive.value = !modalActive.value;
    };

    return { modalActive, toggleModal };
  },
  components: {
    CryptoModal,
  },
};
